namespace $ { export class $mol_app_inventory_controller extends $mol_page {

	/// domain $mol_app_inventory_domain
	@ $mol_mem()
	domain() {
		return new $mol_app_inventory_domain()
	}

	/// position_rows /
	position_rows() {
		return [] as any[]
	}

	/// body <= position_rows
	body() {
		return this.position_rows()
	}

	/// position!id null
	position( id : any ) {
		return <any> null
	}

	/// Position_row!id $mol_app_inventory_position position <= position!id
	@ $mol_mem_key()
	Position_row( id : any ) {
		return new $mol_app_inventory_position().setup( obj => { 
			obj.position = () => this.position(id)
		} )
	}

	/// event_submit?event null
	@ $mol_mem()
	event_submit( event? : any ) {
		return ( event !== void 0 ) ? event : <any> null
	}

	/// submit_label @ \Remove confirmed
	submit_label() {
		return $mol_locale.text( this.locale_contexts() , "submit_label" )
	}

	/// Submit $mol_button_major 
	/// 	event_click?event <=> event_submit?event 
	/// 	sub / <= submit_label
	@ $mol_mem()
	Submit() {
		return new $mol_button_major().setup( obj => { 
			obj.event_click = ( event? : any ) => this.event_submit( event )
			obj.sub = () => [].concat( this.submit_label() )
		} )
	}

	/// Controls_row $mol_row sub / <= Submit
	@ $mol_mem()
	Controls_row() {
		return new $mol_row().setup( obj => { 
			obj.sub = () => [].concat( this.Submit() )
		} )
	}

	/// foot / <= Controls_row
	foot() {
		return [].concat( this.Controls_row() )
	}

} }

